
.App{
  background-color: #0014d2;
  height: 100vh;
}

.bg-pl-logo{
  max-width:  100%;
  margin-top:20vh;
  transform:translate(-10%,-5%) scale(1.6);
  /* position: absolute;
  top:20vh; */
  /* transform: translate(-10%,-50%) scale(1.6);
  top: 50%; */
}

.tracking{
  background-image: url(./tracking/trackingwindow.png);
  background-size:100% 100%;
  background-position:50%;
  background-repeat:no-repeat;
  transform:translateY(-50%);
  top:50%;
  width: 100%;
  position: absolute;
  display: block;
  padding-top: 79.77%;
}

.tracking #tr-input{
  display: block;
  position: absolute;
  width: 86.57%;
  height: 14.39%;
  left: 50%;
  bottom:35.85%;
  transform:translateX(-50%);
}

.track-button{
  background-image: url(./tracking/trackButton.png);
    background-size: 100% 100%;
    background-position: 50%;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    width: 41.83%;
    height: 11.95%;
    left: 50%;
    bottom: 12.44%;
    transform: translateX(-50%);
}

.r-content{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  max-width: 568px;
  margin-top: -300px;
  width: 100%;
  text-align: center;
  height: 65%;
}

.error{
  margin-top: 20px;
  max-width: 60%;
  max-height: 60%;
}

@font-face { 
  font-family: US101;  
  src: url(./tracking/US101.woff);
}

.input-font{
  font-family: US101; 
}